import { request, requestDownload } from 'utils';

const prefix = '/api/organization';

export const getDocIns = ({
  id,
  docInNo,
  docInDate,
  sentPlace,
  noteDate,
  docContent,
  savedPlace,
  recipientUnit,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/DocIn',
    method: 'GET',
    params: {
      id,
      docInNo,
      docInDate,
      sentPlace,
      noteDate,
      docContent,
      savedPlace,
      recipientUnit,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getUser = () =>
  request({
    prefix: '/identity',
    url: `/sign-in`,
    data: { username: 'admin', password: 'admin' },
    method: 'POST',
  });

export const createDocIn = (newDoc, token, headers = {}) =>
  request({
    prefix,
    url: `/DocIn`,
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`,
      ...headers,
    },
    data: { ...newDoc },
    method: 'POST',
  });

export const deleteDocIn = (id, token, headers = {}) =>
  request({
    prefix,
    url: `/DocIn`,
    params: { id },
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`,
      ...headers,
    },
    method: 'DELETE',
  });

export const getDocInId = id =>
  request({
    prefix,
    url: `/DocIn/GetById`,
    params: { id },
    method: 'GET',
  });

export const updateDocIn = (editDoc, token, headers = {}) =>
  request({
    prefix,
    url: `/DocIn`,
    data: { ...editDoc },
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`,
      ...headers,
    },
    method: 'PUT',
  });

export const downloadFile = (paramsSearch, token, headers = {}) =>
  requestDownload({
    prefix,
    defaultFileName: 'ExportDocIns.xlsx',
    url: `/DocIn/ExportDocIns`,
    data: { ...paramsSearch },
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`,
      ...headers,
    },
    method: 'POST',
  });



